export default class extends React.Component {

    static getInitialProps = async ({ query: { id } }) => {
        const response = await fetch(`https://api.audioboom.com/audio_clips/${id}.mp3`);
        const { body: { audio_clip } } = await response.json();
        const title = audio_clip.title;
        const description = audio_clip.description;
        const audio = audio_clip.urls.high_mp3;
        const podcastImage = audio_clip.urls.image
        return { title, description, audio, podcastImage };
    }

    render() {
        const { title, description, audio, podcastImage } = this.props;
        console.log(title)
        return (
            <div className="podcast-wrapper">
                <div className="podcastImage-wrapper">
                    <div>
                        <img src={podcastImage} className="podcastPlayerImage" alt="podcast image player"/>
                    </div>
                </div>

                <div className="player-wrapper">
                    <h5>{ title }</h5>
                    <audio controls src={audio} type="audio/mpeg" />
                </div>
                <style jsx>
                    {`
                        .podcast-wrapper {
                            width: 100%;
                            height: 100vh;
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            align-items: center;
                            background: transparent
                        }
                        
                        .podcastImage-wrapper {
                            width: 100%;
                            height: 100%;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            background: url(${podcastImage}) center no-repeat;
                            background-size: cover
                        }

                        .podcastImage-wrapper div {
                            width: 100%;
                            height: 100%;
                            background: linear-gradient(0deg, rgba(81, 81, 81, 0.65), rgba(34, 34, 34, 0.74)) 0% 0%;
                            display: flex;
                            justify-content: space-around;
                            flex-direction: column;
                            align-items: center;
                            cursor: pointer;
                            padding: 20px 15px 56px;
                            text-align: center;                            
                        }

                        .podcastPlayerImage {
                            width: 200px;
                            height: 200px;
                            border-radius: 4px;
                        }

                        .player-wrapper {
                            width: 100%;
                            height: 220px;
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            align-items: center;                            
                            background: gray;
                        }

                        .player-wrapper audio {
                            outline: none
                        }

                        h5 {
                            color: white;
                        }

                    `}
                </style>

                <style jsx global>
                    {`
                        body {
                            margin: 0;
                            padding: 0;
                            width: 100%;
                            height: 100%;
                            font-family: system-ui;
                        }
                    
                    `}
                </style>
            </div>
        )
    }
}

